<template>
  <div class="blank">
    <div class="question-title">
      <span class="badge">{{required ? '必填' : '选填'}}</span>
      <span class="title">{{ title }}</span>
      <span class="description" v-if="description && description.replace(/\s/g, '')">（{{ description }}）</span>
    </div>
    <Form>
      <FormItem prop="value">
        <xInput
          type="number"
          v-model="integer"
          :max="max"
          :min="min"
          :step="1"
          @on-blur="checkValidate"
          @on-change="inputChange()"
          :readonly="readonly"/>
      </FormItem>
    </Form>
  </div>
</template>

<script>
import validate from '@/api/validate'
import xInput from './xComponents/xInput'

export default {
  name: 'age',
  components: {
    xInput
  },
  data () {
    return {
      uuid: '00000000-0000-0000-0000-0000000000020',
      integer: null,
      isCorrect: true
    }
  },
  props: {
    schemaKey: {
      type: String,
      require: true
    },
    title: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    },
    description: {
      type: String,
      default: ''
    },
    min: {
      type: Number,
      default: 0
    },
    max: {
      type: Number,
      default: 200
    },
    maxLength: {
      type: Number,
      default: 200
    },
    validate: {
      type: Boolean
    },
    readonly: {
      type: Boolean
    },
    readonlyData: {
      type: Array,
      default: () => []
    },
    schemaType: {
      type: String
    },
    apiType: {
      type: String
    },
    required: {
      type: Boolean,
      default: false
    },
    server: {
      type: Boolean,
      require: false
    }
  },
  mounted () {
    if (this.value || (this.value !== null && this.value == 0)) { // eslint-disable-line
      this.integer = parseInt(this.value)
      this.inputChange(true)
    }
  },
  methods: {
    inputChange (first) {
      this.$emit('input', {
        schemaKey: this.schemaKey,
        value: this.integer,
        server: this.server,
        first
      })
    },
    async checkValidate () {
      if (this.validate) {
        const tmp = {}
        tmp[this.schemaKey] = this.integer
        const data = await validate(this.schemaType, this.apiType, {
          projectId: this.$route.query.projectId,
          valueJson: JSON.stringify(tmp)
        })

        if (data.response.success === false && data.response.msg) {
          this.$Message.error(data.response.msg)
        } else if (data.response.success === true && data.response.msg) {
          this.$Message.info(data.response.msg)
        }
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.blank
  padding 0.1rem
  margin auto
  display flex
  flex-direction column
  font-size 1rem
  width 100%
  .question-title
    text-align left
    margin-bottom 0.5rem
    .badge
      display inline-block
      color #ffffff
      background-color #f39938
      height 24px
      line-height 24px
      width 46px
      text-align center
      border-radius 12px
      font-size 13px
    .title
      font-size 14px
      font-weight bold
      margin-left 0.5rem
    .description
      font-size 13px
      color #666
      margin-left 0.5rem
</style>
